import React, { Suspense, lazy, useState } from 'react';
import './ExperiencePage.scss';
import './ExperiencePage.media.scss';
import Text from '../../components/Text/Text';
import skill_1 from '../../img/skill_1.png';
import skill_2 from '../../img/skill_2.png';
import skill_3 from '../../img/skill_3.png';
import skill_4 from '../../img/skill_4.png';
import SkillDescription from '../../components/SkillDescription/SkillDescription';
import Spinner from 'react-svg-spinner';

const Skill = lazy(() => import('../../components/Skill/Skill'));

const ExperiencePage = () => {
	const skills = [
		{
			id: 1,
			img: skill_1,
			name: 'Ninja Coding',
			descr: '"Ninja Coding" -      ,        .          ,     ,      . "Ninja Coding" -       .',
			footer: '    ,      ',
		},
		{
			id: 2,
			img: skill_2,
			name: 'Fixing Bugs',
			descr: '"Fixing Bugs" -  ,      ,   .   "Fixing Bugs",        ,  .  , "Fixing Bugs"   ,        .',
			footer: ' Fixing Bugs,         .             !',
		},
		{
			id: 3,
			img: skill_3,
			name: 'Code Optimization',
			descr: '"Code Optimization" -  ,           .    ,     ,    ,      .  "Code Optimization",     ,    ,   ,    .',
			footer: '   "Code Optimization"    ,     .',
		},
		{
			id: 4,
			img: skill_4,
			name: 'Finding Information',
			descr: '"Finding Information" -  ,           .    ,       ,       .  "Finding Information",      ,            .              .',
			footer: '   "Finding Information"      .     ,     ,     .',
		},
	];

	const [skillActive, setSkillActive] = useState(1);

	const changeSkillActive = (id) => {
		setSkillActive(id);
	};

	return (
		<div className="exp">
			<div className="container">
				<Text
					title=""
					subtitle="   "
					line={true}
					fz36={true}
					center={true}
				/>
				<div className="exp__skill">
					{skills.map((icon) => (
						<Suspense
							key={icon.id}
							fallback={
								<Spinner size="200px" color="deepskyblue" />
							}
						>
							<Skill
								id={icon.id}
								changeSkillActive={changeSkillActive}
								className={
									skillActive === icon.id
										? 'skill skill_active'
										: 'skill'
								}
								name={icon.name}
								key={icon.id}
								skillImg={icon.img}
							/>
						</Suspense>
					))}
				</div>
				<div className="exp__skillDescription">
					{skills.map((descr) => (
						<SkillDescription
							className={
								skillActive === descr.id
									? 'descr descr_active'
									: 'descr'
							}
							key={descr.id}
							title={descr.name}
							body={descr.descr}
							footer={descr.footer}
						/>
					))}
				</div>
			</div>
		</div>
	);
};

export default ExperiencePage;
